@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
	position: relative;
	overflow: hidden;
	max-height: 700rem;
	min-height: 50rem;

	@include m.desktop {
		max-height: 90.8vh;
	}

	.overlay {
		position: absolute;
		background: rgb(165, 3, 255);
		background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%, rgba(0, 212, 255, 1) 100%);
		width: 100%;
		height: 100%;

		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	video {
		width: auto;
		display: block;
		@include m.tablet {
			width: 100%;
		}
	}
}

.contenido-video {
	z-index: 1;

	h2 {
		color: v.$blanco;
		font-size: 70rem;
		margin: 0;
	}

	p {
		background-color: v.$rosa;
		display: inline-block;
		padding: 10rem 20rem;
		color: v.$blanco;
		font-weight: 700;
		font-size: 24rem;
		margin: 0;
	}
}
