@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
	padding: 50rem 0;

	h3 {
		text-align: center;
	}
}

.galeria-imagenes {
	@include m.grid(2, 20rem);
	@include m.telefono {
		@include m.grid(3, 20rem);
	}

	@include m.tablet {
		@include m.grid(4, 20rem);
	}

	@include m.desktop {
		@include m.grid(6, 20rem);
	}
}

.overlay {
	z-index: 2;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	width: 100%;
	height: 100%;

	display: grid;
	place-items: center;

	background-color: rgba(0, 0, 0, 0.4);

	picture {
		display: block;
		max-width: 800rem;
	}
}

.btn-cerrar {
	width: 40rem;
	height: 40rem;
	border-radius: 50%;
	background-color: v.$blanco;
	display: grid;
	place-items: center;
	font-weight: 700;

	&:hover {
		cursor: pointer;
	}
}

body {
	&.fijar-body {
		overflow: hidden;
	}
}
