@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
	background-color: v.$verde;

	h1 {
		color: v.$blanco;
		text-align: center;

		@include m.tablet {
			margin: 0;
		}
	}

	.contenido-header {
		padding: 20rem;

		@include m.tablet {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
}

.nav {
	@include m.tablet {
		position: sticky;
		top: 0;
		z-index: 3;
		box-shadow: 3rem 3rem 10rem #000;
	}
}

.navegacion-principal {
	@include m.tablet {
		display: flex;
		a {
			&:nth-child(1),
			&:nth-child(2) {
				margin-right: 20rem;
			}
		}
	}
	a {
		display: block;
		text-align: center;
		margin-bottom: 10rem;
		color: v.$blanco;
		font-size: 24rem;

		@include m.tablet {
			margin-bottom: 0;
		}

		&:hover {
			color: v.$amarillo;
		}
	}
}
