@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-festival {
	padding: 5rem 0;
	margin: 50rem auto;

	@include m.desktop {
		@include m.grid(2, 50rem);
	}
}

.contenido-festival {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin-top: 50rem;
	@include m.desktop {
		margin-top: 0;
	}

	h2 {
		margin-bottom: 20rem;
	}

	.fecha {
		color: v.$rosa;
		font-weight: 700;
	}
}
