@use 'base/variables' as v;
@use 'base/mixins' as m;

.lineup {
	background-color: v.$rosa;
	padding: 50rem 0;

	h3,
	p {
		text-align: center;
		color: v.$blanco;
	}

	.dia {
		text-transform: uppercase;
		margin: 50rem 0;
	}
}

.escenarios-contenedor {
	@include m.tablet {
		@include m.grid(2, 50rem);
	}

	p {
		color: v.$negro;
	}
}

.escenario {
	padding-top: 20rem;

	p {
		&:after {
			content: ' ';
			display: block;
			width: 200rem;
			height: 100rem;
			background-size: 150rem;
			background-repeat: no-repeat;
			background-position: center center;
			text-align: center;
			margin: 0 auto;
			margin-top: 20rem;
		}
	}
}

.edm {
	.nombre-escenario::after {
		background-image: url(../img/icono_edm.png);
	}
}

.rock {
	.nombre-escenario::after {
		background-image: url(../img/icono_rock.png);
	}
}

.calendario {
	li {
		padding: 25rem;
		font-weight: 700;

		span {
			font-weight: 400;
		}
	}
}

.bg-amarillo {
	background-color: v.$amarillo;

	li {
		&:nth-child(odd) {
			background-color: darken(v.$amarillo, 15%);
		}
	}
}

.bg-verde {
	background-color: v.$verde;

	li {
		&:nth-child(odd) {
			background-color: darken(v.$verde, 10%);
		}
	}
}
