@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos {
	padding: 50rem 0;

	h3 {
		text-align: center;
		color: v.$blanco;
		margin-bottom: 20rem;
	}
}

.contenedor-boletos {
	@include m.tablet {
		@include m.grid(2, 50rem);
	}
}

.pase {
	p {
		text-align: center;
		color: v.$blanco;
		font-size: 28rem;
	}

	&-header,
	&-footer {
		padding: 5rem;
		margin: 0;

		p {
			text-align: center;
			color: v.$blanco;
			margin: 0;
		}
	}

	&-header {
		background-color: v.$rosa;
		p {
			text-transform: uppercase;
			font-size: 16rem;
		}
	}

	&-body {
		ul {
			li,
			:nth-child(odd) {
				background-color: v.$blanco;
				padding: 10rem;
				border-bottom: 1px solid #e1e1e1;
			}
		}
	}

	&-footer {
		background-color: v.$morado;
		p {
			font-size: 24rem;
			font-weight: 700;
		}
	}
}
