html {
  font-size: 6.25%; /*  1rem = 1px */
  box-sizing: border-box; /*hack para box mnodel*/
  scroll-snap-type: y mandatory;
}

*,
*:before,
*:after {
  box-sizing: inherit; /*hack para box mnodel*/
}

body {
  font-size: 16rem;
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
}

.contenedor {
  width: min(95%, 1200rem);
  margin: 0px auto;
}

p {
  font-size: 20rem;
  line-height: 1.5;
}

h1,
h2,
h3 {
  margin: 0 0 50rem 0;
}

h1 {
  font-size: 40rem;
}

h2 {
  font-size: 36rem;
}

h3 {
  font-size: 30rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  background-color: #4cb8b3;
}
.header h1 {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 20rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .nav {
    position: sticky;
    top: 0;
    z-index: 3;
    box-shadow: 3rem 3rem 10rem #000;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
  }
  .navegacion-principal a:nth-child(1), .navegacion-principal a:nth-child(2) {
    margin-right: 20rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin-bottom: 10rem;
  color: #ffffff;
  font-size: 24rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin-bottom: 0;
  }
}
.navegacion-principal a:hover {
  color: #fef46d;
}

.video {
  position: relative;
  overflow: hidden;
  max-height: 700rem;
  min-height: 50rem;
}
@media (min-width: 992px) {
  .video {
    max-height: 90.8vh;
  }
}
.video .overlay {
  position: absolute;
  background: rgb(165, 3, 255);
  background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%, rgb(0, 212, 255) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #ffffff;
  font-size: 70rem;
  margin: 0;
}
.contenido-video p {
  background-color: #f53756;
  display: inline-block;
  padding: 10rem 20rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 24rem;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 0;
  margin: 50rem auto;
}
@media (min-width: 992px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 50rem;
}
@media (min-width: 992px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 20rem;
}
.contenido-festival .fecha {
  color: #f53756;
  font-weight: 700;
}

.lineup {
  background-color: #f53756;
  padding: 50rem 0;
}
.lineup h3,
.lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 50rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

.escenario {
  padding-top: 20rem;
}
.escenario p:after {
  content: " ";
  display: block;
  width: 200rem;
  height: 100rem;
  background-size: 150rem;
  background-repeat: no-repeat;
  background-position: center center;
  text-align: center;
  margin: 0 auto;
  margin-top: 20rem;
}

.edm .nombre-escenario::after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario::after {
  background-image: url(../img/icono_rock.png);
}

.calendario li {
  padding: 25rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: #fef46d;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-verde {
  background-color: #4cb8b3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

.galeria {
  padding: 50rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 20rem;
  }
}

.overlay {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.4);
}
.overlay picture {
  display: block;
  max-width: 800rem;
}

.btn-cerrar {
  width: 40rem;
  height: 40rem;
  border-radius: 50%;
  background-color: #ffffff;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

body.fijar-body {
  overflow: hidden;
}

.boletos {
  padding: 50rem 0;
}
.boletos h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 20rem;
}

@media (min-width: 768px) {
  .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50rem;
  }
}

.pase p {
  text-align: center;
  color: #ffffff;
  font-size: 28rem;
}
.pase-header, .pase-footer {
  padding: 5rem;
  margin: 0;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
}
.pase-header {
  background-color: #f53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 16rem;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 10rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752f97;
}
.pase-footer p {
  font-size: 24rem;
  font-weight: 700;
}

.site-footer {
  background-color: #f53756;
  padding: 10rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #ffffff;
}